@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.form-bg {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* margin: 5px; */
  font-family: 'Poppins', sans-serif;
  font-weight: bolder;
}

.label-bg {
  padding-right: 136px;
  margin-bottom: -20px;
}

.password-label-bg {
  padding-right: 103px;
  margin-bottom: -20px;
}

.email-bg {
  border-radius: 6px;
  background-color: #f5efe7;
  padding: 7px;
  /* margin: 9px; */
}

.error-bg {
  color: red;
  font-weight: 300;
  padding-right: 23px;
}

.pass-bg {
  color: red;
  font-weight: 300;
  padding-left: 132px;
}

.password-bg {
  border-radius: 6px;
  background-color: #f5efe7;
  /* margin-left: 72px; */
  padding: 7px;
}

.confirm_password-bg {
  border-radius: 6px;
  background-color: #f5efe7;
  /* margin-left: 72px; */
  padding: 7px;
}

.confirm-password-label-bg {
  padding-right: 33px;
  margin-bottom: -20px;
}

.confirm-pass-bg {
  color: red;
  font-weight: 300;
  padding-left: 11px;
}

button {
  margin: 0 auto;
  display: block;
  background-color: bisque;
  padding: 6px;
    border-radius: 12px;
}